body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier, monospace;
  background: linear-gradient(90deg, #d219fc 0%, #00c613 100%);
	background-size: cover;
}


button {
  cursor: pointer;
}

.Tilt {
  background: linear-gradient(90deg, #d219fc 0%, #00c613 100%);
}

.form {
  width: 700 px;
background: linear-gradient(90deg, #d219fc 0%, #24fc19 100%);
}

.bounding_box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.App {
  text-align: center;
}

.header {
	display: flex;
	justify-content: space-between;
}

.logo {
	width: 9vw;
	height: 9vw;
}

.center {
  display: flex;
  justify-content: center;
}

button {
  background-color: #d219fc;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}


@media screen and (max-width: 800px) {
	.logo {
		width: 12vw;
		height: 12vw;
	}
	.f3 {
		font-size: 1.3rem;
	}
}

@media screen and (max-width: 650px) {
	.logo {
		width: 14vw;
		height: 14vw;
	}
}

@media screen and (max-width: 450px) {
	.logo {
		width: 19vw;
		height: 19vw;
	}
	.f3 {
		font-size: 1rem;
	}
}

